<template>
  <div  class="w-full h-auto">
    <div v-for="items in list" :key="items">
      <div class="grid grid-cols-2 grid-rows-4 gap-1 sm:grid sm:grid-cols-2 sm:grid-rows-4 sm:gap-4">
        <div v-for="value in items" :key="value">
          <div class="w-full" style="height: 6.5rem"><img @error="notShow" :src="`${value.news_image[0].url}`" alt="" class="w-full h-full"></div>
          <p class="message text-black text-start font-thin px-1" :style="{fontSize:size+'px'}">{{value.title}}</p>
          <div class="flex flex-row align-center justify-between w-full">
            <div><span class="text-start font-thin text-black" style="font-size: 10px">2 ساعت قبل</span></div>
            <div><div class="flex flex-row align-center">
              <font-awesome-icon :icon="['fas','comment']" class="text-slate-500 font-semibold" :style="{fontSize:size+'px'}"></font-awesome-icon>
              <span class="font-thin text-black" :style="{fontSize:size+'px'}">{{value.news_comment_count}}</span>
              <font-awesome-icon :icon="['fas','eye']" class="text-slate-500 font-semibold" :style="{fontSize:size+'px'}"></font-awesome-icon>
              <span class="font-thin text-black" :style="{fontSize:size+'px'}">{{value.visit}}</span>
            </div></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import{FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import {ref} from "vue";
export default {
  name: "NumberTransfer",
  props:{
    list:{
      type:Array,
      required:true,
      default:null
    }
  },
  components:{FontAwesomeIcon},
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    const size=ref(11)
    return{
      size,notFound,notShow
    }
  }
}
</script>

